import React, {Component} from 'react';
import ReactHowler from 'react-howler';
import {volumeSliderStyle} from '../js/input';

class Sound extends Component{
  constructor(props){
    super(props);

    this.state = {
      name: this.props.sound.name,
      fade: 1,
      editingName: false
    }
    this.nameInputKeypress = this.nameInputKeypress.bind(this);
    this.renderProgress = this.renderProgress.bind(this);
  }
  componentDidMount(){
    this.timer = setInterval(this.renderProgress,300);
  }
  componentWillUnmount(){
    clearInterval(this.timer);
  }

  renderProgress(){
    if(this.props.sound.playing){
      this.forceUpdate();
    }
  }

  //Checks for enter-key press when editing sound name
  nameInputKeypress(e){
    const {sound} = this.props;
    //Gets the keycode, if code is 13 (enter), it processes the name change
    const code = (e.keyCode ? e.keyCode : e.which);
    if(code === 13){
      const value = e.target.value.trim();
      if(value !== ""){sound.changeName(value);}
      this.setState({editingName: false});
    }
  }

  render(){
    const {sound,effect} = this.props;

    const name = () =>{
      if(this.state.editingName){
        return <input type="text" onKeyPress={this.nameInputKeypress} ref={(input) => {this.editNameInput = input}} onChange={(e)=>this.setState({name:e.target.value})} onBlur={()=> this.setState({editingName:false})} value={this.state.name}></input>;
      }
      return <h3 onClick={()=>this.setState({editingName:true})}>{sound.name}</h3>;
    }

    const progress = () =>{
      if( typeof this.player  !== 'undefined' && this.player.howlerState() === 'loaded'){
        const x = (this.player.seek() / this.player.duration()) * 100;
        return {width: x+"%"}
      }
      return {};
    }

    const volStyles = () =>{
      return volumeSliderStyle(sound.volume);
    }
    const volIcon = () =>{
      if(sound.mute){
        return "i-mx-volume-x muted";
      }
      if(sound.soloMute){
        return "i-mx-volume-x";
      }

      if(sound.volume > 75){
        return "i-mx-volume-2";
      }else if(sound.volume > 25){
        return "i-mx-volume-1";
      }else{
        return "i-mx-volume-1";
      }
    }
    const vol = (effect.volume/100) * (sound.volume/100) * this.state.fade;
    const loopIcon = () =>{
      if(sound.loop){
        return "i-mx-repeat looping"
      }else{
        return 'i-mx-repeat'
      }
    }

    const mediaButton = () =>{
      if(sound.playing){
        return <a className="sound-pause" onClick={sound.pause}><i className="fas fa-pause"></i></a>;
      }else{
        return <a className="sound-play" onClick={sound.play}><i className="fas fa-play"></i></a>;
      }
    }

    return(
      <div className="sound-card">
        <span className="sound-progress" style={progress()}></span>
        <div className="sound-head">
          <p className="title">{sound.name}</p>
          <div className="float-right options-container">
            <a className="sound-upload" onClick={sound.replace}><i className="i-mx-upload"></i></a>
            <a className="sound-close" onClick={sound.remove}><i className="i-mx-times"></i></a>
          </div>
        </div>
        <div className="sound-body">
          <div className="sound-controls">
            <ul className="inline">
              <li>
                {mediaButton()}
              </li>
              <li>
                <a className="sound-stop" onClick={()=>sound.stop(this.player)}><i className="fas fa-stop"></i></a>
              </li>
              <li className="ml-3">
                <a className="sound-loop font-size-14" onClick={sound.loopToggle}><i className={loopIcon()}></i></a>
              </li>
              <li className="sound-volume">
                <a className="sound-mute" onClick={sound.muteToggle}><i className={volIcon()}></i></a>
                <input type="range" min="0" max="100" style={volStyles()} value={sound.volume} onChange={(e)=>sound.adjustVolume(e.target.value)}/>
              </li>
            </ul>
          </div>
        </div>
        <ReactHowler
          src={sound.url}
          format={[sound.ext]}
          playing={sound.playing}
          volume={vol}
          mute={sound.mute || effect.mute || sound.soloMute ? true : false}
          loop={sound.loop}
          html5={false}
          preload={false}
          onLoad={sound.onLoad}
          onEnd={()=>sound.onFinish(this.player)}
          ref={(ref) => (this.player = ref)}/>
      </div>

      // <div className="">
      //   <div className="sound-file_header">
      //     {name()}
      //     <button type="button" name="replace" className="replace-file-button" onClick={sound.replace}><i className="fas fa-upload"></i></button>
      //     <button type="button" name="delete" className="delete-button" onClick={sound.remove}> <i className="fas fa-times"></i> </button>
      //   </div>
      //   <div className="sound-file_body">
      //     <div className="sound-file_progress" style={progress()}></div>
      //     <div className="sound-file_content">
      //       <div className="sound-file_controls">
      //         {mediaControls()}
      //       </div>
      //       <div className="sound-file_volume">
      //         <i className={volIcon()} onClick={sound.muteToggle}></i>
      //         <input type="range" min="0" max="100" name="" style={volStyles()} value={sound.volume} className="volume-slider" onChange={(e)=>sound.adjustVolume(e.target.value)}/>
      //       </div>
      //       <div className="sound-file_options">
      //         <i className={loopIcon()} onClick={sound.loopToggle}></i>
      //         <button onClick={sound.soloToggle}>{sound.solo.toString()}</button>
      //       </div>
      //     </div>
      //   </div>
      //   <ReactHowler
      //     src={sound.url}
      //     format={[sound.ext]}
      //     playing={sound.playing}
      //     volume={vol}
      //     mute={sound.mute || effect.mute || sound.soloMute ? true : false}
      //     loop={sound.loop}
      //     html5={false}
      //     preload={false}
      //     onLoad={sound.onLoad}
      //     onEnd={()=>sound.onFinish(this.player)}
      //     ref={(ref) => (this.player = ref)}/>
      // </div>
    );
  }
}

export default Sound;



